* {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-inline-start: 0;
  text-decoration: none;
}
body {
  color: var(--brand-color-gray);
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  letter-spacing: var(--brand-letter-spacing);
}
img {
  display: block;
}
.container {
  margin: 0px auto;
  width: 100%;
  height: 100%;
  min-width: 320px;
  padding: 0 20px;
}

