
.products {
    &__container {
      display: flex;
      flex-direction: column;
      gap: 0px;
      padding: 60px 20px;
    }
    &__title-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }
    &__label {
      font-family: 'Titan One';
      font-size: 18px;
      line-height: 1.166;
      text-align: center;
      text-transform: uppercase;
      color: var(--brand-color-pink-label);
    }
    &__title {
      font-family: 'Titan One';
      font-style: normal;
      font-weight: 400;
      font-size: 34px;
      line-height: 1.147;
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: var(--brand-color-red);
    }
    &__grid {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    &__item {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 40px;
      padding-top: 160px;
      text-align: center;
      line-height: 1.1875;
      border-radius: 24px;
      margin-top: 140px;
      transition: transform 0.3s ease-in-out;
      &:hover {
        transform: translateY(-10px);
        box-shadow: 4px 4px 20px var(--brand-color-red);
        cursor: pointer;
      }
    }
    &__item:hover &__button {
      background-color: var(--brand-color-red);
      color: var(--brand-color-white);
      transform: rotate(-90deg);
      cursor: pointer;
      border-radius: 25%;
    }
    &__item--cream {
      background-color: var(--brand-color-pink-dark);
    }
    &__item--coffee {
      background-color: #f0d1a5;
    }
    &__item--shake {
      background-color: #c2e297;
    }
    &__item:nth-child(1):before {
      content: '';
      position: absolute;
      left: 50%;
      top: -29%;
      transform: translateX(-50%);
      z-index: 60;
      width: 280px;
      height: 280px;
      background-image: url(..//images/products/P1-M-X1.png);
    }
    &__item:nth-child(2):before {
      content: '';
      position: absolute;
      left: 50%;
      top: -29%;
      transform: translateX(-50%);
      z-index: 60;
      width: 280px;
      height: 280px;
      background-image: url(..//images/products/P2-M-X1.png);
    }
    &__item:nth-child(3):before {
      content: '';
      position: absolute;
      left: 50%;
      top: -29%;
      transform: translateX(-50%);
      z-index: 60;
      width: 280px;
      height: 280px;
      background-image: url(..//images/products/P3-M-X1.png);
    }
    &__name {
      font-family: 'Titan One';
      font-weight: 400;
      font-size: 20px;
      line-height: 1.15;
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: var(--brand-color-white)
    }
    &__marker {
      position: relative;
      margin: 30px auto;
      width: 6px;
      height: 6px;
      background-color: var(--brand-color-white);
      border-radius: 50%;
      &:before,
      &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: inherit;
        border-radius: inherit;
      }
      &:before {
        right: 10px;
        top: 0px;
      }
      &:after {
        left: 10px;
        top: 0px;
      }
      transition: 350ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
      transition-property: z-index;
    }
    &__description {
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 152%;
      text-align: center;
      letter-spacing: 0.04em;
      color: var(--brand-color-white)
    }
    &__button {
      display: flex;
      align-items: center;
      padding-left: 11.5px;
      width: 40px;
      height: 40px;
      margin-top: 32px;
      border-color: transparent;
      border-radius: 50%;
      background-color: var(--brand-color-white);
      color: var(--brand-color-red);
      transition: background-color 250ms linear;
      transition: transform 0.3s ease-in-out;
    }
    &__icon {
      fill: currentColor;
      transition: fill 250ms linear;
    }
  }
  @media screen and (min-width: 768px) {
    .products__item {
      flex-basis: calc((100% - 2 * 16px) / 3);
    }
  }
  @media screen and (min-width: 1200px) {
    .products__item {
      flex-basis: calc((100% - 2 * 32px) / 3);
    }
  }
  
  @media (min-width: 768px) {
