      &__label {
        font-size: 26px;
        line-height: 1.153;
      }
      &__title {
        font-size: 48px;
        line-height: 1.145;
      }
      &__item {
        padding-top: 118px;
        margin-top: 150px;
      }
    }
  }
  @media (min-width: 1200px) {
    .products {
      &__item:nth-child(1):before {
